@import url("https://fonts.googleapis.com/css2?family=Chivo+Mono&family=Roboto:wght@700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.register-page {
  background-image: linear-gradient(90deg, #03c4f4, rgb(6, 238, 149));
  height: 100vh;
}

.register-page > Form {
  background-image: linear-gradient(90deg, rgb(6, 238, 149), #03c4f4);
  padding: 50px 50px;
  border: 2px solid black;
  border-radius: 5px;
}
.register-page > Form:hover {
  background-image: linear-gradient(90deg, rgb(0, 255, 157), #05ccfd);
}
.btn:hover {
  background: #4303f4;
  color: #fff;
  font-weight: 700;
  border-radius: 5px;
  box-shadow: 0 0 5px #4303f4, 0 0 25px #4303f4, 0 0 50px #4303f4,
    0 0 100px #4303f4;
}
.d-flex {
  flex-direction: column;
}

.btn {
  margin: 20px 65px 0px 65px;
  color: black;
  font-family: 'Chivo Mono', monospace;
}

h1 {
  font-family: "Chivo Mono", monospace;
}
.toRegister{
    color: rgb(2, 2, 2);
    text-decoration: none;
    font-family: 'Chivo Mono', monospace;
}

.toRegister:hover{
    color: #fff;
    background-image: linear-gradient(90deg, rgb(35, 206, 140), #1caace);
    box-shadow: 0 0 5px #1caace, 0 0 25px #1caace, 0 0 50px #1caace,
    0 0 100px #4303f4;
}