@import url("https://fonts.googleapis.com/css2?family=Chivo+Mono&family=Roboto:wght@700&display=swap");

.navbar {
  background-color: rgb(134, 156, 149) !important;
}

.logout-btn {
  margin-bottom: 20px !important;
  background-color: rgb(173, 199, 192);
  border: 2px solid rgb(117, 138, 131);
}

.logout-btn:hover {
    background: #5a726e !important;
    color: #fff;
    font-weight: 700;
    border-radius: 5px;
    box-shadow: 0 0 5px #4b4855, 0 0 25px #827c92, 0 0 50px #aca5be,
      0 0 100px #d1cddd !important;
}

.navbar-brand {
  color: #fff;
  font-family: "Chivo Mono", monospace;
  font-size: 30px;
}
