* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.content {
  height: 100vh;
}

.register-page {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
}

/* transaction page */
.filters {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px gray;
}

/* antd icons */

.switch-icon {
  border: 1px solid rgba(0, 0, 0, 0.712);
  border-radius: 5px;
  padding: 10px 15px;
}

.switch-icon:hover{
  background-color: #eeeeee;
}
.anticon svg {
  font-size: 20px;
  cursor: pointer;
}

.active-icon {
  color: black;
}

.inactive-icon {
  color: gray;
}

.addnew-btn{
  background-color: rgb(219, 218, 217) ;
  border: 2px solid rgb(216, 215, 214);
  margin-bottom: 20px !important;
}

.addnew-btn:hover{
  background-color: rgb(177, 176, 175) !important;
  border: rgb(177, 176, 175) !important;
  padding: 8.5px 15px !important;
  
  box-shadow: 0 0 5px #5f5d66, 0 0 25px #828085, 0 0 50px #acaab1,
    0 0 100px #d8d8d8 !important;
}

