@import url("https://fonts.googleapis.com/css2?family=Chivo+Mono&family=Roboto:wght@700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.login {
  background-image: url("https://images.unsplash.com/photo-1634733988138-bf2c3a2a13fa?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=870&q=80") !important;
  height: 100vh !important;
}

.login > Form {
  background-image: linear-gradient(
    90deg,
    rgb(218, 139, 44),
    rgb(234, 143, 36)
  ) !important;
  padding: 50px 50px !important;
  border: 2px solid black !important;
  border-radius: 5px !important;
}
.login > Form:hover {
  background-image: linear-gradient(
    90deg,
    rgb(252, 163, 54),
    rgb(252, 141, 15)
  ) !important;
}
.loginbtn:hover {
  color: #fff;
  font-weight: 700;
  border: #fa931d;
  margin-bottom: 4px;
  background-color: #ff7300 !important;
  box-shadow: 0 0 5px rgb(253, 166, 4), 0 0 25px #fcb93c, 0 0 50px #ffaf1c,
    0 0 100px #f8d3a3 !important;
}
.d-flex {
  flex-direction: column;
}

.loginbtn {
  margin: 20px 65px 0px 65px;
  color: black;
  font-family: "Chivo Mono", monospace;
  background-color: rgb(252, 186, 6);
  border: 2px solid rgb(252, 166, 6);
}

h1 {
  font-family: "Chivo Mono", monospace;
}
.toLogin {
  color: rgb(2, 2, 2);
  padding: 5px;
  border-radius: 5px;
  text-decoration: none;
  font-family: "Chivo Mono", monospace;
}

.toLogin:hover {
  color: #fff;
  background-image: linear-gradient(90deg, #fca33e, #fa931d);
  box-shadow: 0 0 5px #ffb968, 0 0 25px #ffb968, 0 0 50px #ffb968,
    0 0 100px #ffb968;
}
